import React from 'react';
import Pokemon from './Pokemon';
import PropTypes from 'prop-types';


class PokeList extends React.Component {
  render () {
    const newArray = this.props.arrayList.map((pokemon) => {
        return (
          <li key={pokemon.id} className="pokemon__item pokemon__item--container">
            <Pokemon pokemonURl={pokemon.url} pokemonName={pokemon.name} pokemonType={pokemon.types} />
          </li>

        )
      })
    return newArray;
  }
}

PokeList.propTypes = {
  arrayList: PropTypes.arrayOf(PropTypes.object)
};

export default PokeList;
